import React from 'react'
import axios from 'axios';


class OrderModal extends React.Component {


    completeOrder = () => {
      axios.patch(`orders`, {id: this.props.order.id, status: 'complete'})
      .then(res => {
        this.props.onClose();
        this.props.onComplete();
      })
    }

    render() {
        return <div className="fixed z-10 inset-0 overflow-y-auto" aria-labelledby="modal-title" role="dialog" aria-modal="true">
          <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" aria-hidden="true"></div>
        
            <span className="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>
        
          
            <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg h-auto sm:w-full">
              <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                <div className="sm:flex sm:items-start">
                  <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">

                    <h3 className="text-lg leading-6 font-medium text-gray-900 text-left" id="modal-title">
                      Order #{('00' + this.props.order.id).substr(-3)}
                    </h3>
                   
                        <div className="-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
                            <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
                            <div className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
                                <table className="min-w-full divide-y divide-gray-200">
                                <thead className="bg-gray-50">
                                    <tr>
                                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                        Item
                                    </th>
                                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                        Subtotal
                                    </th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-200">

                                    {this.props.order.items.map((item,index)=>{
                                        return <tr key={item.item.id}>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                                {item.quantity} {item.item.name}
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 text-right">
                                                {(item.quantity * item.item.price).toLocaleString('en-US', {
                                                style: 'currency',
                                                currency: 'PHP',
                                                })}
                                            </td>
                                        </tr>
                                    })}
                                </tbody>
                                </table>
                            </div>
                            <div className="px-6 py-2 text-right">
                                Total : {parseFloat(this.props.order.total).toLocaleString('en-US', {
                                                style: 'currency',
                                                currency: 'PHP',
                                                })}
                            </div>
                            </div>
                        </div>

                  </div>
                </div>
              </div>
              <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                <button type="button" className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-yellow-900 text-base font-medium text-white hover:bg-yellow-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-yellow-900 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.completeOrder}>
                  Complete
                </button>
                <button type="button" className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.props.onClose}>
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        ;
    }
}

export default OrderModal;
