import React from 'react'
import Moment from 'react-moment';
import OrderModal from './OrderModal';
import axios from 'axios';
import icon from '../icon.jpg';

class OrderItem extends React.Component {

    state = {
        order: null,
        showOrderModal: false
    }

    handleClose = () => {
        this.setState({ showOrderModal: false });
    }

    handleOrderClick = (id, e) => {
        axios.get(`orders?id=${id}`)
          .then(res => {
            const order = res.data;
            this.setState({ order });
            this.setState({ showOrderModal: true });
          })
    }

    render() {
        return <>{this.state.showOrderModal === true &&
            <OrderModal order={this.props.order} onComplete={this.props.onComplete}  onClose={this.handleClose}  />
        }
        <tr onClick={(e) => this.handleOrderClick(this.props.order.id, e)}>
            <td className="px-6 py-4 whitespace-nowrap">
                <div className="flex items-center">
                    <div className="flex-shrink-0 h-10 w-10">
                        <img className="h-10 w-10 rounded-full" src={icon} alt="" />
                    </div>
                    <div className="ml-4">
                        <div className="text-sm font-medium text-gray-900">
                            Order #{('00' + this.props.order.id).substr(-3)}
                        </div>
                        
                        <div className="text-sm text-gray-500">
                            <Moment fromNow>{this.props.order.created_at}</Moment>
                        </div>
                    </div>
                </div>
            </td>
        </tr>
        </>
        ;
    }
}

export default OrderItem;
