import React from 'react'
import axios from 'axios';
import OrderItem from './OrderItem';
import CreateModal from './CreateModal';

class OrderList extends React.Component {
    state = {
        orders: [],
        items: [],
        showAddModal: false
    }

    handleAddOrderClick = () => {
        this.setState({ showAddModal: true });

    }

    handleAddOrderConfirmClick = () => {
        axios.get(`orders?status=pending`)
          .then(res => {
            const orders = res.data;
            this.setState({ orders });
          })

        this.setState({ showAddModal: false });

    }

    handleAddOrderCancelClick = () => {
        this.setState({ showAddModal: false });

    }

    handleOrderComplete = () => {
        axios.get(`orders?status=pending`)
          .then(res => {
            const orders = res.data;
            this.setState({ orders });
          })
    }

    componentDidMount() {

        axios.get(`items`)
          .then(res => {
            const items = res.data;
            this.setState({ items });
          })

        axios.get(`orders?status=pending`)
          .then(res => {
            const orders = res.data;
            this.setState({ orders });
          })
      }

    render() {
        return <>
        {this.state.showAddModal === true &&
            <CreateModal items={this.state.items} onConfirm={this.handleAddOrderConfirmClick} onClose={this.handleAddOrderCancelClick} />
        }
        <div className="flex flex-col">
            <a onClick={this.handleAddOrderClick} className="m-2 inline-flex items-center justify-center px-5 py-3 border border-transparent text-base font-medium rounded-md text-white bg-yellow-900 hover:bg-green-700">
            Add New Order
            </a>
            <div className="-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
                <div className="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
                    <div className="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
                        <table className="min-w-full divide-y divide-gray-200">
                            <tbody className="bg-white divide-y divide-gray-200">
                                {this.state.orders.map((order,index)=>{
                                    return <OrderItem key={order.id} order={order} onComplete={this.handleOrderComplete} onClick={this.handleOrderClick}/>
                                })}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div></>;
    }
}

export default OrderList;
