import React from 'react'
import axios from 'axios';

class CreateModal extends React.Component {

  state = {
    items: this.props.items,
    total: 0
  }

  reset = () => {
    let items = this.state.items;

    this.setState({
      total: 0
    })

    for (var i = 0; i < items.length; i++) {
      items[i].quantity = 0;
    }
    this.setState({
      total: 0,
      items: items,
    });

    console.log(items);
  }

  cancel = () => {
    this.reset();
    this.props.onClose();
  }

  create = () => {
    let items = [];
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].quantity) {
        items.push({
          id: this.state.items[i].id,
          quantity: this.state.items[i].quantity
        })
      }
    }

    if (items.length) {
      axios.post(`orders`, {
        items: items
      }).then(res => {
        this.reset();
        this.props.onConfirm();
      });
    }

  }

  addItem = (item, e) => {
    this.setState({ total: this.state.total + parseFloat(item.price) });
    let items = this.state.items;
    for (var i = 0; i < items.length; i++) {
      if (item.id === items[i].id) {
        if (!items[i].quantity) {
          items[i].quantity = 0;
        }
        items[i].quantity++;
      }
    }
  }

  removeItem = (item, e) => {

    if (!item.quantity) {
      return;
    }

    this.setState({ total: this.state.total - parseFloat(item.price) });
    let items = this.state.items;
    for (var i = 0; i < items.length; i++) {
      if (item.id === items[i].id) {
        if (!items[i].quantity) {
          items[i].quantity = 0;
        }
        items[i].quantity--;
      }
    }
  }

  componentDidMount() {

  }

  render() {
    return <div className="fixed z-10 inset-0 overflow-y-auto" aria-labelledby="modal-title" role="dialog" aria-modal="true">
      <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" aria-hidden="true"></div>

        <span className="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>


        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg h-auto sm:w-full">
          <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
            <div className="sm:flex sm:items-start">
              <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                <ul>
                  {this.state.items.map((item, index) => {
                    return <li key={item.id}>
                      <div className="flex flex-nowrap">
                        <div className="flex-1 pt-3">
                          {item.name}
                        </div>
                        <div className="flex flex-1">
                          <div className="flex-1">
                            <a onClick={(e) => this.addItem(item, e)} className="m-2 inline-flex items-center justify-center px-1 py-1 border border-transparent text-base font-medium rounded-md text-white bg-yellow-900 hover:bg-green-700">
                              +
                                                </a>
                          </div>
                          <div className="flex-1 pt-3">
                            {item.quantity || 0}
                          </div>
                          <div className="flex-1">
                            <a onClick={(e) => this.removeItem(item, e)} className="m-2 inline-flex items-center justify-center px-1 py-1 border border-transparent text-base font-medium rounded-md text-white bg-yellow-900 hover:bg-green-700">
                              -
                                                </a>
                          </div>
                        </div>


                      </div>
                    </li>
                  })}

                </ul>

                <br />
                    Total: {parseFloat(this.state.total).toLocaleString('en-US', {
                  style: 'currency',
                  currency: 'PHP',
                })}

              </div>
            </div>
          </div>
          <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
            <button type="button" className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-yellow-900 text-base font-medium text-white hover:bg-yellow-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-yellow-900 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.create}>
              Create Order
                </button>
            <button type="button" className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm" onClick={this.cancel}>
              Cancel
                </button>
          </div>
        </div>
      </div>
    </div>
      ;
  }
}

export default CreateModal;
